import { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Show } from '../../components/Show';
import { Tags } from '../../components/Tags/Tags';
import { Toggler } from '../../components/Toggler/Toggler';
import { Joke } from '../../models/joke.model';
import { ReduxState } from '../../rootReducer';
import './Jokes.css';
import { ActionTypes } from './JokesReducer';

const filterByTitle = (jokes, title) => jokes.filter(setlist => setlist.title.search(new RegExp(title, 'gi')) > -1);

function Jokes() {
  const jokes = useSelector<ReduxState, Joke[]>(state => state.jokes.jokes);
  const activeJoke = useSelector<ReduxState, Joke | undefined>(state => state.jokes.jokes.find(joke => joke.active));
  const [searchText, setSearchText] = useState('')
  const saving = useSelector<ReduxState, boolean>(state => state.jokes.saving);
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch({ type: ActionTypes.fetchJokes });
  }, [dispatch]);

  return (
    <div className="jokes flex-1-1-0 has-top-border d-flex align-stretch">
      <div className="jokes__left-panel d-flex flex-d-column align-stretch">
        <h2 className="jokes__page-title">My Jokes</h2>
        <ul className="jokes__jokes-list flex-1-1-0 bg-2 has-y-scrollbar d-flex flex-d-column">
          <input 
            value={searchText}
            onChange={e => setSearchText(e.target.value)}
            className="jokes__list-item jokes__search bg-2" 
            placeholder='Search'
          />
          {filterByTitle(jokes, searchText).map(joke => 
            <li 
              onClick={() => dispatch({ type: ActionTypes.setActiveJoke, payload: joke })} 
              className={`jokes__list-item jokes__list-item${joke.active ? '--active' : ''}`} 
              key={joke.id}
            >
              {joke.title}
            </li>
          )}
          <li 
            className="jokes__list-item d-flex align-center justify-center" 
            onClick={() => dispatch({ type: ActionTypes.addJoke })}
          >
            <img src="/icon-add.svg" alt="add icon" />Add a new Joke
          </li>
        </ul>
      </div>
      <div className="jokes__right-panel flex-1-1-0 d-flex flex-d-column align-stretch">
        <Show if={!activeJoke}>
          <textarea 
            className="jokes__joke-text jokes__joke-text--empty bg-2 flex-1-1-0" 
            disabled
            value='Joke Not Selected'
            readOnly
          ></textarea>
        </Show>
        <Show if={!!activeJoke}>
          <div className="jokes__right-header d-flex">
            <span>Joke Title</span>
            <input 
              className="jokes__joke-title-input bg-2 flex-1-1-0" 
              value={activeJoke?.title}
              onChange={e => dispatch({ type: ActionTypes.updateJoke, payload: { ...activeJoke, title: e.target.value } })}
            />
          </div>
          <textarea 
            className="jokes__joke-text bg-2 flex-1-1-0 has-y-scrollbar" 
            value={activeJoke?.text ?? ''}
            onChange={e => dispatch({ type: ActionTypes.updateJoke, payload: { ...activeJoke, text: e.target.value } })}
          ></textarea>
          <div className="jokes__prompts">
            <span>Prompts: &nbsp;</span> <Tags 
              postfix='Prompt'
              tags={activeJoke?.prompts} 
              onChange={tags => dispatch({ type: ActionTypes.updateJoke, payload: {...activeJoke, prompts: tags }})} 
            />
          </div>
          <div className="jokes__sort-tags">
            <span>Sort tags: &nbsp;</span><Tags
              postfix='Tag'
              prefix='#' 
              tags={activeJoke?.sortTags} 
              onChange={tags => dispatch({ type: ActionTypes.updateJoke, payload: {...activeJoke, sortTags: tags }})} 
            />
          </div>
          <div className="jokes__estimated-time">
            <span>Estimated time</span><span className="label">Minutes</span>
            <input 
              value={activeJoke?.estimatedTime.minutes} 
              onChange={e => dispatch({ type: ActionTypes.updateJoke, payload: { ...activeJoke, estimatedTime: { ...activeJoke?.estimatedTime, minutes: e.target.value}}})} 
              className="jokes__minutes jokes__joke-title-input bg-2 flex-1-1-0" 
            />
            <small className="label">Seconds</small>
            <input 
              value={activeJoke?.estimatedTime.seconds} 
              onChange={e => dispatch({ type: ActionTypes.updateJoke, payload: { ...activeJoke, estimatedTime: { ...activeJoke?.estimatedTime, seconds: e.target.value}}})} 
              className="jokes__seconds jokes__joke-title-input bg-2 flex-1-1-0" 
            />
          </div>
          <div className="jokes__new-material">
            New Material &nbsp;&nbsp;<Toggler 
              onChange={newValue => dispatch({ type: ActionTypes.updateJoke, payload: { ...activeJoke, newMaterial: newValue } })} 
              value={activeJoke?.newMaterial}
            />
          </div>
          <div className="jokes__save d-flex justify-end">
            <span 
              className={`d-flex align-center clickable ${activeJoke ? '' : 'hidden'}`} 
              onClick={() => dispatch({ type: ActionTypes.saveJokes, payload: jokes })}
            >
              Save 
              <Show if={!saving}><img className="jokes__save-icon" alt="save icon" src="/icon-save.svg"/></Show>
              <Show if={saving}><img className="jokes__save-icon" alt="save icon" src="/icon-loading.svg"/></Show>
            </span>
            <span 
              className={`d-flex align-center ml-100 clickable ${activeJoke ? '' : 'hidden'}`}
              onClick={() => dispatch({ type: ActionTypes.deleteJoke, payload: activeJoke })}
            >
              Delete <img alt="delete icon" src="/icon-delete.svg"/>
            </span>
          </div>
        </Show>
      </div>
    </div>
  )
}

export { Jokes }