import { Fragment, useState } from 'react';
import { Link } from 'react-router-dom';
import { RoutesEnum } from '../../models/routes.model';
import { Show } from '../Show';
import './Menu.css';

function Menu() {
  const [open, setOpen] = useState(false);

  return <Fragment>
    <button 
      onClick={() => setOpen(!open)} 
      className={`menu__burger-btn d-block clickable ${open ? 'menu__burger-btn--open' : ''}`}
    ></button>
    <Show if={open}>
      <div className={`menu d-flex`}>
        <Link to={RoutesEnum.Jokes} onClick={() => setOpen(false)} className='menu__item clickable'>Jokes</Link>
        <Link to={RoutesEnum.Setlists} onClick={() => setOpen(false)} className='menu__item clickable'>Setlists</Link>
        <Link to={RoutesEnum.Recordings} onClick={() => setOpen(false)} className='menu__item clickable'>Recodings</Link>
      </div>
    </Show>
  </Fragment>
}

export { Menu };