import './Toggler.css';

interface TogglerProps {
  value: boolean,
  onChange: (value: boolean) => void
}

function Toggler(togglerProps: Partial<TogglerProps>) {
  return (
    <span 
      className={`toggler ${togglerProps.value ? 'toggler--checked': '' }`} 
      onClick={() => togglerProps.onChange?.(!togglerProps.value)}>
      <span className="toggler__thumb"></span>
    </span>
  );    
}

export { Toggler };