import { Route, Routes } from 'react-router-dom';
import './App.css';
import { RoutesEnum } from './models/routes.model';
import { Jokes } from './routes/Jokes/Jokes';
import { Setlists } from './routes/Setlists/Setlists';
import { Recording } from './routes/Recording/Recording';
import { SetlistC } from './routes/Setlist/Setlist';
import { Menu } from './components/Menu/Menu';

function App() {
  return (
    <main className='container d-flex flex-d-column'>
      <section className='d-flex flex-1-1-0'>
        <Menu />
        <Routes>
          <Route path={RoutesEnum.Home} element={<Jokes />}></Route>
          <Route path={RoutesEnum.Jokes} element={<Jokes />}></Route>
          <Route path={RoutesEnum.Setlists} element={<Setlists />}></Route>
          <Route path={`${RoutesEnum.Setlist}/:setlistId`} element={<SetlistC />}></Route>
          <Route path={`${RoutesEnum.Recording}/:setlistId`} element={<Recording />}></Route>
        </Routes>
      </section>
    </main>
  );
}

export default App;
