import { call, put, takeLatest } from "redux-saga/effects";
import { Joke } from "../../models/joke.model";
import { ActionTypes } from "./JokesReducer";
import { Storage } from "../../utils/Storage";
import { Action } from "../../models/action.model";

const storage = new Storage();

function* fetchJokesWorker() {
  const jokes: Joke[] = yield call(() => storage.get('jokes'));
  yield put({ type: ActionTypes.setJokes, payload: jokes });
}

function* saveJokesWorker(action: Action) {
  const jokes = action.payload;
  yield put({ type: ActionTypes.setJokesSaving, payload: true });
  yield call(() => storage.set('jokes', jokes));
  yield put({ type: ActionTypes.setJokesSaving, payload: false });
}

export function* jokesSaga() {
  yield takeLatest(ActionTypes.saveJokes, saveJokesWorker)
  yield takeLatest(ActionTypes.fetchJokes, fetchJokesWorker)
}