import { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useParams } from 'react-router-dom';
import { Tags } from '../../components/Tags/Tags';
import { Duration, Joke } from '../../models/joke.model';
import { Setlist } from '../../models/setlist.model';
import { ReduxState } from '../../rootReducer';
import { ActionTypes as SetlistActionTypes } from '../Setlists/SetlistsReducer';
import { ActionTypes as JokesActionTypes } from '../Jokes/JokesReducer';
import './Setlist.css';
import _ from 'lodash';
import { Toggler } from '../../components/Toggler/Toggler';
import { Show } from '../../components/Show';

const filterByTitle = (jokes, title) => jokes.filter(joke => joke.title.search(new RegExp(title, 'gi')) > -1);
const filterByTags = (jokes, tags) => jokes.filter(joke => !tags.length || tags.every(tagItem => joke.sortTags.includes(tagItem)));
const filterByNewMaterial = (jokes, newMaterial) => jokes.filter(jokes => jokes.newMaterial === newMaterial);
const calculateEstimatedTime = (jokes) => jokes.reduce((acc, currJoke: Joke) => {
  acc.minutes += Number(currJoke.estimatedTime.minutes) ?? 0;
  acc.seconds += Number(currJoke.estimatedTime.seconds) ?? 0;

  if (acc.seconds >= 60) {
    acc.minutes += Math.floor(acc.seconds / 60);
    acc.seconds %= 60;
  }

  return acc;
}, { minutes: 0, seconds: 0 });
const formatDuration = (duration: Duration) => `${duration.minutes <= 9 ? '0': ''}${duration.minutes}:${duration.seconds <= 9 ? '0': ''}${duration.seconds}`;
const parseDuration = (duration: string) => {
  const [minutes, seconds] = duration.split(':');
  return {
    minutes: Number(minutes), 
    seconds: Number(seconds)
  }
}
const filterExistingJokes = (jokes: Joke[], existingJokes: Joke[]) => jokes.filter(joke => !existingJokes.includes(joke));

function SetlistC() {
  const { setlistId } = useParams();
  const jokes = useSelector<ReduxState, Joke[]>(state => state.jokes.jokes);
  const setlists = useSelector<ReduxState, Setlist[]>(state => state.setlists.setlists);
  const activeSetlist = useSelector<ReduxState, Setlist>(state => state.setlists.setlists.find(setlist => setlist.id === setlistId));
  const saving = useSelector<ReduxState, boolean>(state => state.setlists.saving);
  const [searchText, setSearchText] = useState('');
  const [length, setLength] = useState(formatDuration(activeSetlist.length));
  const [lightMeAt, setLightMeAt] = useState(formatDuration(activeSetlist.lightMeAt));
  const [tags, setTags] = useState<string[]>([]);
  const [newMaterial, setNewMaterial] = useState<boolean>(false);
  const dispatch = useDispatch();
  
  useEffect(() => {
    dispatch({ type: JokesActionTypes.fetchJokes });
  }, [dispatch]);

  return (
    <div className="setlist flex-1-1-0 d-flex has-top-border">
      <div className="setlist__panel setlist__panel-left flex-1-1-0 d-flex flex-d-column">
        <h3 className="setlist__title">My Jokes</h3>
        <ul className="setlist__jokes-list bg-2 has-y-scrollbar">
          <input 
            value={searchText}
            onChange={e => setSearchText(e.target.value)}
            className="setlist__list-item setlist__search bg-2" 
            placeholder='Search'
          />
          { filterExistingJokes(filterByNewMaterial(filterByTags(filterByTitle(jokes, searchText), tags), newMaterial), activeSetlist.jokes).map(joke => (
            <li
              key={joke.id}
              onDoubleClick={() => dispatch({ type: SetlistActionTypes.updateSetlist, payload: {
                ...activeSetlist, 
                jokes: _.uniqBy([...activeSetlist.jokes, joke], 'id')
              }})}
              className="clickable setlist__list-item"
            >{ joke.title }</li>
          ))}
        </ul>
        <div className="setlist__tags-container d-flex">
          <h3>Sort Tags</h3>
          <div className="setlist__tags flex-1-1-0 d-flex flex-wrap">
            <Tags 
              prefix='#' 
              postfix='Tag' 
              tags={tags}
              onChange={newTags => setTags([...newTags])}
            />
          </div>
        </div>
        <div className="setlist__new-material d-flex">
          New Material &nbsp;&nbsp;<Toggler 
            onChange={newValue => setNewMaterial(newValue)} 
            value={newMaterial}
          />
        </div>
      </div>
      <div className="setlist__panel setlist__panel-right flex-1-1-0 d-flex flex-d-column">
        <div className="setlist__title d-flex">
          <span>Setlist Title</span>
          <input 
            className="setlist__setlist-title-input bg-2 flex-1-1-0" 
            value={activeSetlist?.title}
            onChange={e => dispatch({ type: SetlistActionTypes.updateSetlist, payload: { ...activeSetlist, title: e.target.value } })}
          />
        </div>
        <ul className="setlist__setlist-jokes-list bg-2 has-y-scrollbar">
          {
            activeSetlist?.jokes.map(
              (joke, idx) => (
                <li 
                  title={joke.text} 
                  key={joke.id} 
                  className="setlist__list-item d-flex"
                >
                  <span>{idx+1}.{joke.title}</span>
                  <img 
                    onClick={() => dispatch({ type: SetlistActionTypes.updateSetlist, payload: {
                      ...activeSetlist, 
                      jokes: activeSetlist.jokes.filter(item => item.id !== joke.id)
                    }})}
                    className="setlist__delete-joke clickable" 
                    src="/icon-delete.svg" 
                    alt="icon delete" 
                  />
                </li>
              )
            )
          }
        </ul>
        <div className="d-flex setlist__time align-center">
          <div className="setlist__set-length">
            <span className="label">Set length</span>
            <input 
              value={length} 
              onChange={e => setLength(e.target.value)} 
              className="setlist__input bg-2" 
            />
          </div>
          <div className="setlist__estimated-time">
            <span className="label">Estimated time</span>
            <input 
              readOnly
              value={formatDuration(calculateEstimatedTime(activeSetlist.jokes))} 
              className="setlist__input bg-2" 
            />
          </div>
        </div>
        <div className="d-flex setlist__time align-center">
          <span className="label">Light me at</span>
          <input 
            value={lightMeAt} 
            onChange={e => setLightMeAt(e.target.value)} 
            className="setlist__input bg-2" 
          />
        </div>
        <div className="setlist__action-buttons d-flex">
          <div 
            className="d-flex align-center clickable setlist__action-button" 
            onClick={() => dispatch({ type: SetlistActionTypes.saveSetlists, payload: setlists.map(slist => {
              if (slist.id === activeSetlist.id) {
                return { 
                  ...activeSetlist,
                  lightMeAt: parseDuration(lightMeAt),
                  length: parseDuration(length)
                }
              }

              return { ...slist };
            }) })}
          >
            Save Setlist
            <Show if={!saving}><img className="jokes__save-icon" alt="save icon" src="/icon-save.svg"/></Show>
            <Show if={saving}><img className="jokes__save-icon" alt="save icon" src="/icon-loading.svg"/></Show>
          </div>
        </div>
      </div>
    </div>
  )
}

export { SetlistC }