import { call, put, takeLatest } from "redux-saga/effects";
import { Setlist } from "../../models/setlist.model";
import { ActionTypes } from "./SetlistsReducer";
import { Storage } from "../../utils/Storage";
import { Action } from "../../models/action.model";

const storage = new Storage();

function* fetchSetlistsWorker() {
  const setlists: Setlist[] = yield call(() => storage.get('setlists'));
  yield put({ type: ActionTypes.setSetlists, payload: setlists });
}

function* saveSetlistsWorker(action: Action) {
  const setlists = action.payload;
  yield put({ type: ActionTypes.setSetlistsSaving, payload: true });
  yield call(() => storage.set('setlists', setlists));
  yield put({ type: ActionTypes.setSetlistsSaving, payload: false });
}

export function* setlistsSaga() {
  yield takeLatest(ActionTypes.saveSetlists, saveSetlistsWorker)
  yield takeLatest(ActionTypes.fetchSetlists, fetchSetlistsWorker)
}