import { useEffect, useState } from 'react';
import './Recording.css';
import { Show } from '../../components/Show';
import { Time } from '../../components/Time';
import { useDispatch, useSelector } from 'react-redux';
import { ReduxState } from '../../rootReducer';
import { Duration } from '../../models/joke.model';
import { ActionTypes } from '../Jokes/JokesReducer';
import { useParams } from 'react-router-dom';
import { Setlist } from '../../models/setlist.model';

const durationToSec = (duration: Duration) => Number(duration.seconds) + Number(duration.minutes * 60);

function Recording() {
  const { setlistId } = useParams();
  const activeSetlist = useSelector<ReduxState, Setlist>(state => state.setlists.setlists.find(setlist => setlist.id === setlistId));
  const [isRecording, setIsRecording] = useState(false);
  const [seconds, setSeconds] = useState(0);
  const [interval, setIntervalState] = useState(null);
  const [activeJoke, setActiveJoke] = useState<string | null>();
  const [arePromptsVisible, setArePromptsVisible] = useState(false);
  const [isBlinking, setIsBlinking] = useState(false);
  const [hasTimerBeenLighted, setHasTimerBeenLighted] = useState(false);
  const dispatch = useDispatch();
  const jokesSeconds = activeSetlist.jokes.map(joke => durationToSec(joke.estimatedTime));
  const length = durationToSec(activeSetlist.length);
  let lightMeAt = durationToSec(activeSetlist.lightMeAt);
  
  useEffect(() => {
    dispatch({ type: ActionTypes.fetchJokes });
    return () => window.clearInterval(interval);
  }, [dispatch, interval]);
  let activeJokeIndex: number;

  const startTimer = () => {
    setIntervalState(setInterval(() => {
      setSeconds(sec => {
        if (sec >= length) {
          setIntervalState(interval => {
            window.clearInterval(interval);
            return interval;
          });

          return sec;
        }

        return sec + 1;
      });

      // Active Joke Switch Logic
      if (!activeJokeIndex) {
        activeJokeIndex = 0;
        setActiveJoke(activeSetlist.jokes[activeJokeIndex].id);
      }

      jokesSeconds[activeJokeIndex] --;
      if (jokesSeconds[activeJokeIndex] <= 0 && jokesSeconds[activeJokeIndex + 1]) {
        activeJokeIndex ++;
        setActiveJoke(activeSetlist.jokes[activeJokeIndex].id);
      }

      // Blinking Logic 
      lightMeAt --;
      if (lightMeAt <= 0 && !hasTimerBeenLighted) {
        setHasTimerBeenLighted(true);
        setIsBlinking(true);
      }
    }, 1000));
  }

  const stopTimer = () => {
    window.clearInterval(interval);
    setSeconds(0);
  }

  return (
    <div className="recording flex-1-1-0 d-flex flex-d-column justify-stretch">
      <div 
        className="recording__start-top-button d-flex align-center justify-center clickable"
        onClick={async () => {
          isRecording ? stopTimer() : startTimer();
          setIsRecording(!isRecording)
        }}
      >
        <Show if={isRecording}>Stop</Show>
        <Show if={!isRecording}>Start</Show>
        &nbsp;timer
      </div>
      <div className={`recording__time clickable ${isBlinking ? 'blinking' : ''}`}>
        <Time seconds={seconds} />
      </div>
      {
        activeSetlist.jokes.filter((_, idx) => idx < 7).map((joke, idx) => <div 
          className={`recording__joke ${activeJoke === joke.id ? 'recording__joke--active has-stroke' : ''}`} 
          key={joke.id}
        >
          <span 
            className={`${activeJoke === joke.id ? 'has-stroke' : ''} clickable`}
            onClick={() => setArePromptsVisible(!arePromptsVisible)}
          >{joke.title}</span>
          <Show if={activeJoke === joke.id && arePromptsVisible}>
            <ul className={`recording__prompts d-flex flex-d-row`}>
              {joke.prompts.map(prompt => <li key={prompt} className='recording__prompt'>{prompt}</li>)}
            </ul>
          </Show>
        </div>)
      }
    </div>
  )
}

export { Recording }