export class Storage {
  private delayedDb: Promise<IDBDatabase>;
  private dbName = 'laughpad';
  private objectStoreName = 'laughpad';

  constructor() {
    const request = indexedDB.open(this.dbName);
    this.delayedDb = new Promise(resolve => request.addEventListener('success', (e: Event) => resolve((e.target as any).result as IDBDatabase)));
    request.addEventListener('upgradeneeded', e => {
      const db = (e.target as any).result;
      db.createObjectStore(this.objectStoreName);
    });
  }

  async get(key: string): Promise<any> {
    const db = await this.delayedDb;
    const transaction = db.transaction([this.objectStoreName], "readwrite");
    const objectStore = transaction.objectStore(this.objectStoreName);
    const request = objectStore.get(key);
    
    return new Promise(resolve => {
      request.addEventListener('success', event => resolve((event.target as any).result));
    });
  }

  async set(key: string, value: any): Promise<void> {
    const db = await this.delayedDb;
    const transaction = db.transaction([this.objectStoreName], "readwrite");
    const objectStore = transaction.objectStore(this.objectStoreName);
    const request = objectStore.put(value, key);
    
    return new Promise(resolve => {
      request.addEventListener('success', () => resolve());
    });
  }
}