import { Action } from "../../models/action.model";
import { Joke } from "../../models/joke.model";
import { generateId } from "../../utils/IdGenerator";

export enum ActionTypes {
  fetchJokes = 'Jokes__fetch',
  saveJokes = 'Jokes__save',
  setJokes = 'Jokes__set', 
  setActiveJoke = 'Jokes__setActiveJoke',
  updateJoke = 'Jokes__update',
  deleteJoke = 'Jokes__delete', 
  addJoke = 'Jokes__add', 
  setJokesSaving = 'Jokes__setSaving'
}

function createEmptyJoke(): Joke {
  return {
    id: generateId(5),
    title: 'New Joke',
    text: '',
    prompts: [],
    sortTags: [],
    estimatedTime: { minutes: 0, seconds: 0},
    newMaterial: false,
    active: true
  }
}

export interface JokesState {
  jokes: Joke[],
  saving: boolean
}

const initialState: JokesState = {
  jokes: [],
  saving: false
}

export function jokesReducer(state: JokesState = initialState, action: Action) {
  switch (action.type) {
    case ActionTypes.setJokes: 
      return { ...state, jokes: [ ...action.payload ] };
      
    case ActionTypes.setActiveJoke: 
      const activeJoke = action.payload as Joke;
      return {...state, jokes: state.jokes.map(joke => ({ ...joke, active: activeJoke.id === joke.id })) };

    case ActionTypes.updateJoke: 
      const jokeToUpdate = action.payload as Joke;
      return { 
        ...state, 
        jokes: state.jokes.map(joke => {
          if (joke.id === jokeToUpdate.id) {
            return { ...jokeToUpdate }
          } 
          return joke;
        }) 
      };

    case ActionTypes.addJoke: 
      return {
        ...state, 
        jokes: [ ...state.jokes.map(joke => ({ ...joke, active: false })), createEmptyJoke() ] 
      };
    
    case ActionTypes.deleteJoke:
      const jokeToDelete = action.payload as Joke;
      let jokeIndex = state.jokes.findIndex(joke => joke.id === jokeToDelete.id);
      const jokes = state.jokes.filter(joke => joke.id !== jokeToDelete.id).map(joke => ({...joke, active: false}));
      while(!jokes[jokeIndex] && jokeIndex >= 0) {
        jokeIndex --;
      }

      return {
        ...state, 
        jokes: jokes.map((joke, index) => ({ ...joke, active: index === jokeIndex })) 
      };

    case ActionTypes.setJokesSaving: 
      return {
        ...state, 
        saving: action.payload
      }

    default: 
      return { ...state }
  }
}