import { Fragment, useState } from "react"
import _ from 'lodash';

import "./Tags.css";

export interface TagsProps {
  tags: string[] | undefined,
  onChange?: (tags: string[]) => void, 
  prefix?: string,
  postfix?: string
}

export function Tags(props: TagsProps) {
  const [newTag, setNewTag] = useState('');

  return (
    <Fragment>
      {
        _.uniq(props.tags).map(tag => <span 
          key={tag} 
          className="tags__tag bg-3"
        >
          <span>{props.prefix}{tag}</span>
          <strong 
            className="tags__tag-close" 
            onClick={() => props.onChange?.(_.uniq(props.tags.filter(item => item !== tag)))}
          >&#10006;</strong>
        </span>)
      }
      <input 
        value={newTag}
        onChange={e => setNewTag(e.target.value)}
        onKeyDown={e => {
          if (e.key === 'Enter') {
            props.onChange(_.uniq([...props.tags, newTag]));
            setNewTag('');
          }
        }} 
        placeholder={`New ${props.postfix ?? 'One'}`} 
        className="tags__tag bg-2" 
      />
    </Fragment>
    
  );
}