import createSagaMiddleware from 'redux-saga'

import { all } from 'redux-saga/effects';
import {applyMiddleware, combineReducers, createStore} from 'redux';
import { jokesReducer, JokesState } from './routes/Jokes/JokesReducer';
import { jokesSaga } from './routes/Jokes/JokesSaga';
import { setlistsReducer, SetlistsState } from './routes/Setlists/SetlistsReducer';
import { setlistsSaga } from './routes/Setlists/SetlistsSaga';

export interface ReduxState {
  jokes: JokesState,
  setlists: SetlistsState
}

const rootReducer = combineReducers({
  jokes: jokesReducer, 
  setlists: setlistsReducer
});

export function* rootSaga() {
  yield all([
    jokesSaga(),
    setlistsSaga()
  ])
}

const sagaMiddleware = createSagaMiddleware()
const store = createStore(rootReducer, {}, applyMiddleware(sagaMiddleware));

sagaMiddleware.run(rootSaga)

export { store };
