import { Action } from "../../models/action.model";
import { Setlist } from "../../models/setlist.model";
import { generateId } from "../../utils/IdGenerator";

export enum ActionTypes {
  fetchSetlists = 'Setlists__fetch',
  saveSetlists = 'Setlists__save',
  setSetlists = 'Setlists__set', 
  setActiveSetlist = 'Setlists__setActiveSetlist',
  updateSetlist = 'Setlists__update',
  deleteSetlist = 'Setlists__delete', 
  addSetlist = 'Setlists__add', 
  setSetlistsSaving = 'Setlists__setSaving'
}

export function createEmptySetlist(id = generateId(5)): Setlist {
  return {
    id,
    jokes: [],
    title: 'New Setlist',
    lightMeAt: { minutes: 0, seconds: 15 },
    length: { minutes: 0, seconds: 0}
  }
}

export interface SetlistsState {
  setlists: Setlist[],
  saving: boolean
}

const initialState: SetlistsState = {
  setlists: [],
  saving: false
}

export function setlistsReducer(state: SetlistsState = initialState, action: Action) {
  switch (action.type) {
    case ActionTypes.setSetlists: 
      return { ...state, setlists: [ ...action.payload ] };
      
    case ActionTypes.setActiveSetlist: 
      const activeSetlist = action.payload as Setlist;
      return {...state, setlists: state.setlists.map(setlist => ({ ...setlist, active: activeSetlist.id === setlist.id })) };

    case ActionTypes.updateSetlist: 
      const setlistToUpdate = action.payload as Setlist;
      return { 
        ...state, 
        setlists: state.setlists.map(setlist => {
          if (setlist.id === setlistToUpdate.id) {
            return { ...setlistToUpdate }
          } 
          return setlist;
        }) 
      };

    case ActionTypes.addSetlist: 
      return {
        ...state, 
        setlists: [ 
          ...state.setlists.map(setlist => ({ 
            ...setlist, active: false 
          })), 
          createEmptySetlist(action.payload) 
        ] 
      };
    
    case ActionTypes.deleteSetlist:
      const setlistToDelete = action.payload as Setlist;
      let setlistIndex = state.setlists.findIndex(setlist => setlist.id === setlistToDelete.id);
      const setlists = state.setlists.filter(setlist => setlist.id !== setlistToDelete.id).map(setlist => ({...setlist, active: false}));
      while(!setlists[setlistIndex] && setlistIndex >= 0) {
        setlistIndex --;
      }

      return {
        ...state, 
        setlists: setlists.map((setlist, index) => ({ ...setlist, active: index === setlistIndex })) 
      };

    case ActionTypes.setSetlistsSaving: 
      return {
        ...state, 
        saving: action.payload
      }

    default: 
      return { ...state }
  }
}