import { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Link, useNavigate } from 'react-router-dom';
import { Show } from '../../components/Show';
import { RoutesEnum } from '../../models/routes.model';
import { Setlist } from '../../models/setlist.model';
import { ReduxState } from '../../rootReducer';
import { generateId } from '../../utils/IdGenerator';
import './Setlists.css';
import { ActionTypes } from './SetlistsReducer';

const filterByTitle = (setlists, title) => setlists.filter(setlist => setlist.title.search(new RegExp(title, 'gi')) > -1);

function Setlists() {
  const setlists = useSelector<ReduxState, Setlist[]>(state => state.setlists.setlists);
  const activeSetlist = useSelector<ReduxState, Setlist | undefined>(state => state.setlists.setlists.find(setlist => setlist.active));
  const [searchText, setSearchText] = useState('')
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch({ type: ActionTypes.fetchSetlists });
  }, [dispatch]);
  const navigate = useNavigate();
  
  const createNewSetlist = () => {
    const id = generateId(5);
    dispatch({ type: ActionTypes.addSetlist, payload: id });
    navigate(`${RoutesEnum.Setlist}/${id}`);
  }

  return (
    <div className="setlists flex-1-1-0 d-flex has-top-border">
      <div className="setlists__panel setlists__panel-left flex-1-1-0 d-flex flex-d-column">
        <h3 className="setlists__title">My Setlists</h3>
        <ul className="setlists__setlists-list bg-2 has-y-scrollbar">
          <input 
            value={searchText}
            onChange={e => setSearchText(e.target.value)}
            className="setlists__list-item setlists__search bg-2" 
            placeholder='Search'
          />
          { filterByTitle(setlists, searchText).map(setlist => (
            <li
              key={setlist.id}
              onClick={() => dispatch({ type: ActionTypes.setActiveSetlist, payload: setlist })}
              className={`clickable setlists__list-item ${setlist.active ? 'setlists__list-item--active' : ''}`}
            >{ setlist.title }</li>
          ))}
        </ul>
      </div>
      <div className="setlists__panel setlists__panel-right flex-1-1-0 d-flex flex-d-column">
        <h3 className="setlists__title">Setlist Preview</h3>
        <ul className="setlists__jokes-list bg-2 has-y-scrollbar">
          {activeSetlist?.jokes.map(joke => <li title={joke.text} key={joke.id} className="setlists__list-item">{joke.title}</li>)}
        </ul>
        <Show if={!!activeSetlist}>
          <Link to={`${RoutesEnum.Recording}/${activeSetlist?.id}`} className="setlists__button mr-322">Perform Setlist</Link>
        </Show>
        <button onClick={() => createNewSetlist()} className="setlists__button mr-322"><img src="/icon-add.svg" alt="icon add" />&nbsp;&nbsp;Create New</button>
        <Show if={!!activeSetlist}>
          <Link to={`${RoutesEnum.Setlist}/${activeSetlist?.id}`} className="setlists__button mr-322">Edit this setlist</Link>
        </Show>
      </div>
    </div>
  )
}

export { Setlists }